  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  align-items: center;
  padding: 10px;
  cursor: pointer;
  border-bottom: 1px solid var(--color-gray);
  background-color: var(--color-background);
}
.chat-item:hover {
  background-color: var(--color-background-darken);
}
.chat-item__photo {
  margin-right: 10px;
}
.chat-item__photo img {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  object-fit: cover;
}
.chat-item__info {
  flex-grow: 1;
}
.chat-item__name {
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.chat-item__last-message {
  color: var(--color-text);
  margin-top: 5px;
}
.chat-item__status {
  margin-left: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.chat-item__time {
  font-size: 12px;
  color: var(--color-gray);
  margin-left: 10px;
  white-space: nowrap;
  align-self: flex-start;
}

.unread-count {
  background-color: gray;
  color: var(--color-light);
  border-radius: 50%;
  font-size: 12px;
  height: 20px;
  width: 20px;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.message-status-icon {
  align-self: flex-start;
